import {useEffect, useState} from "react";
import BigNumber from "bignumber.js";

export interface QuellensteuerProps {
	income: number,
	canton: string,
	isMarried: boolean,
	countKids: number,
	isChurchMember: boolean,
}

export interface Steuerergebnis {
	betrag: BigNumber,
	prozent: BigNumber,
}

export class Steuersatz {
	steuerbaresEinkommenLower: BigNumber;
	steuerbaresEinkommenUpper: BigNumber;
	steuersatzProzent: BigNumber;
	steuernAbsolut: BigNumber;

	constructor(steuerbaresEinkommen: BigNumber, tarifschritt: BigNumber, steuersatzProzent: BigNumber, steuernAbsolut: BigNumber) {
		this.steuerbaresEinkommenLower = steuerbaresEinkommen;
		this.steuerbaresEinkommenUpper = steuerbaresEinkommen.plus(tarifschritt);
		this.steuersatzProzent = steuersatzProzent;
		this.steuernAbsolut = steuernAbsolut;
	}

	public isWithin(actualIncome: BigNumber) : boolean {
		return actualIncome.isGreaterThanOrEqualTo(this.steuerbaresEinkommenLower) && actualIncome.isLessThan(this.steuerbaresEinkommenUpper);
	}

	public getSteuer(income : BigNumber) : BigNumber {
		if(this.steuernAbsolut.isZero() && this.steuersatzProzent.isZero()) {
			return new BigNumber(0);
		}

		if(this.steuernAbsolut.isZero()) {
			return income.multipliedBy(this.steuersatzProzent);
		}

		if(this.steuersatzProzent.isZero()) {
			return this.steuernAbsolut;
		}
		return income.multipliedBy(this.steuersatzProzent);
	}

	public toString = () : string => {
		return `Steuersatz UntereGrenze: ${this.steuerbaresEinkommenLower} ObereGrenze: ${this.steuerbaresEinkommenUpper} SteuernAbsolut: ${this.steuernAbsolut} Prozent: ${this.steuersatzProzent}`;
	}
}

export const Quellensteuer = (props: QuellensteuerProps): JSX.Element => {

	const [tax, setTax] = useState<Steuerergebnis>({prozent: new BigNumber(0), betrag: new BigNumber(0)});

	// const { setAlert } = useContext(AlertContext);
	useEffect(() => {
		var path = "/taxdata/tar22" + props.canton.toLowerCase() + ".txt";
		const request = new XMLHttpRequest();
		request.open('GET', path);
		request.send();

		request.onload = () => {
			let strings : string[] = request.responseText.split("\n");
			const tarifcode = buildTarifcode(props.isMarried, props.countKids, props.isChurchMember);
			let map = strings.map(line => matchLine(line, tarifcode));

			let incomePerMonth = new BigNumber(props.income).dividedBy(12);

			let steuersatz = map.find(tarif => tarif?.isWithin(incomePerMonth));
			if (steuersatz) {
				setTax({betrag: steuersatz.getSteuer(incomePerMonth), prozent: steuersatz.steuersatzProzent});
				console.log("Steuersatz gefunden: " + steuersatz);
			} else {
				console.log("kein Steuersatz gefunden!")
				setTax({prozent: new BigNumber(0), betrag: new BigNumber(0)})
			}
		};
		request.onerror = () => {
			console.log("request hat nicht geklappt!");
		}
	}, [props.isChurchMember, props.canton, props.income, props.countKids, props.isMarried]);

	return (
		<tr>
			<td>Quellensteuer</td>
			<td>{new Intl.NumberFormat('de-CH', { style: 'percent', minimumFractionDigits: 0, maximumFractionDigits: 2 }).format(tax.prozent.toNumber())}</td>
			<td>{new Intl.NumberFormat('de-CH', { style: 'currency', currency: 'CHF' }).format(tax.betrag.toNumber())}</td>
		</tr>
);
}

function buildTarifcode(isVerheiratet : boolean, countKids : number, isChurchMember : boolean) {
	var tarifcode = "";
	tarifcode = tarifcode + (isVerheiratet ? "B" : "A");
	tarifcode = tarifcode + countKids;
	tarifcode = tarifcode + (isChurchMember ? "Y" : "N");
	return tarifcode;
}

function matchLine(line: string, tarif: string) : Steuersatz | null {
	const regexp = new RegExp('^0601([A-Z]{2})(' + tarif + ')\\s{7}[0-9]{8}([0-9]{7})([0-9]{2})([0-9]{7})([0-9]{2})\\s([0-9]{2})([0-9]{7})([0-9]{2})([0-9]{3})([0-9]{2})\\s\\s\\s');

	let regExpExecArray = regexp.exec(line);
	if (regExpExecArray === null) {
		return null;
	}

	let canton = regExpExecArray[1];
	let tarifcode = regExpExecArray[2];
	let steuerbaresEinkommenFranken = regExpExecArray[3];
	let steuerbaresEinkommenRappen = regExpExecArray[4];
	let tarifschrittFranken =  regExpExecArray[5];
	let tarifschrittRappen = regExpExecArray[6];
	let countChildren = regExpExecArray[7];
	let steuernFranken = regExpExecArray[8];
	let steuernRappen = regExpExecArray[9];
	let prozentSteuern = regExpExecArray[10];
	let prozentSteuernKommastellen = regExpExecArray[11];

	const steuerbaresEinkommen = new BigNumber(steuerbaresEinkommenFranken + '.' + steuerbaresEinkommenRappen);
	const tarifschritt = new BigNumber(tarifschrittFranken + '.' + tarifschrittRappen);
	const steuernAbsolut = new BigNumber(steuernFranken + '.' + steuernRappen);
	const steuersatz = new BigNumber(prozentSteuern + '.' + prozentSteuernKommastellen).dividedBy(100);

	return new Steuersatz(steuerbaresEinkommen, tarifschritt, steuersatz, steuernAbsolut);
}


