import React, {useEffect, useState} from 'react';
import './App.css';

import {Quellensteuer} from "./abgaben/Quellensteuer";
import {BVG} from "./abgaben/BVG";
import {AHV} from "./abgaben/AHV";
import {ALV1} from "./abgaben/ALV1";
import {
	MantineProvider,
	NumberInput,
	Select,
	Space,
	Stack,
	Radio,
	Table,
	Container,
	AppShell,
	SimpleGrid,
	Header
} from "@mantine/core";
import BigNumber from "bignumber.js";

const defaultAge = 30;
const defaultCountKids = 0;
const defaultIncome = 80_000;

function App() {
	const [income, setIncome] = useState(80_000);
	const [isVerheiratet, setIsVerheiratet] = useState(false);
	const [isKirche, setIsKirche] = useState(false);
	const [age, setAge] = useState<number>(defaultAge);
	const [canton, setCanton] = useState<string | null>("AG");
	const [countKids, setCountKids] = useState(0);

	// print form props
	useEffect(() => {
		console.log("Aktuelle Werte: ")
		console.log("income: " + income)
		console.log("isVerheiratet: " + isVerheiratet);
		console.log("isKirche: " + isKirche);
		console.log("age: " + age);
		console.log("canton: " + canton);
		console.log("countKids: " + countKids);
	}, [income, isVerheiratet, isKirche, age, canton, countKids])

	return (<>
		<MantineProvider withGlobalStyles withNormalizeCSS>
			<AppShell       padding="md"
							header={<Header height={60} p="xs">{/* Header content */}</Header>}>
				<Container size="md" style={{paddingTop: 40, paddingBottom: 40}}>
					<SimpleGrid cols={2}>
						<Stack>
							<NumberInput label={"Bruttojahreslohn"} defaultValue={defaultIncome} min={1} step={1000}
										 onChange={element => element ? setIncome(element!!) : setIncome(defaultIncome)}/>
							<NumberInput label={"Alter"} defaultValue={defaultAge} min={18} max={99}
										 onChange={element => element ? setAge(element!!) : setAge(defaultAge)}/>
							<NumberInput label={"Anzahl Kinder"} defaultValue={defaultCountKids} min={0} max={9}
										 onChange={element => element ? setCountKids(element!!) : setCountKids(defaultCountKids)}/>
							<Select label="Kanton" defaultValue={"AG"}
									data={[{value: "AG", label: "Aargau"}, {value: "AI", label: "Appenzell Innerrhoden"}, {
										value: "AR", label: "Appenzell Ausserrhoden"
									}, {value: "BE", label: "Bern"}, {value: "BL", label: "Basel-Land"}, {value: "BS", label: "Basel-Stadt"}, {
										value: "FR", label: "Freiburg"
									}, {value: "GE", label: "Genf"}, {value: "GL", label: "Glarus"}, {value: "GR", label: "Graubünden"}, {
										value: "JU", label: "Jura"
									}, {value: "LU", label: "Luzern"}, {value: "NE", label: "Neuenburg"}, {value: "NW", label: "Nidwalden"}, {
										value: "OW", label: "Obwalden"
									}, {value: "SG", label: "St. Gallen"}, {value: "SH", label: "Schaffhausen"}, {value: "SO", label: "Solothurn"}, {
										value: "SZ", label: "Schwyz"
									}, {value: "TG", label: "Thurgau"}, {value: "TI", label: "Tessin"}, {value: "UR", label: "Uri"}, {
										value: "VD", label: "Waadt"
									}, {value: "VS", label: "Wallis"}, {value: "ZG", label: "Zug"}, {value: "ZH", label: "Zürich"},]}
									onChange={setCanton}>
							</Select>

							<Radio.Group
								label="Kirchenmitglied"
								withAsterisk
								defaultValue={"false"}
								onChange={element => element === "true" ? setIsKirche(true) : setIsKirche(false)}
							>
								<Radio value="true" label="Ja"/>
								<Radio value="false" label="Nein"/>
							</Radio.Group>

							<Radio.Group
								label="Verheiratet"
								withAsterisk
								defaultValue={"false"}
								onChange={element => element === "true" ? setIsVerheiratet(true) : setIsVerheiratet(false)}
							>
								<Radio value="true" label="Ja"/>
								<Radio value="false" label="Nein"/>
							</Radio.Group>
						</Stack>
						<Stack>
							<Table striped>
								<thead>
								<tr>
									<th>Abzug</th>
									<th>Prozentsatz</th>
									<th>Betrag</th>
								</tr>
								</thead>
								<tbody>
								<tr>
									<td>Bruttolohn pro Monat</td>
									<td></td>
									<td>{new Intl.NumberFormat('de-CH', { style: 'currency', currency: 'CHF' }).format(new BigNumber(income).dividedBy(12).toNumber())}</td>
								</tr>
								<Quellensteuer income={income} canton={canton ? canton : ""} isMarried={isVerheiratet} countKids={countKids}
											   isChurchMember={isKirche}/>
								<AHV income={income}/>
								<ALV1 income={income}/>
								<BVG income={income} age={age ? age : 0}/>
								</tbody>
								<tfoot>
								<tr>
									<th>Summe der Abzüge</th>
									<th>TBD</th>
									<th>TBD</th>
								</tr>
								</tfoot>
							</Table>
						</Stack>
					</SimpleGrid>
					<Space h="md"/>

				</Container>
			</AppShell>
		</MantineProvider>
	</>);
}

export default App;
